const state = {
  getRtl: state => { // whole page direction
    return state.rtl
  },
  getAppIsCollapsed: state => {
    return state.appIsCollapsed
  },
  getAppIsCollapsedClass: state => { // sdiebar toggle effect on .app with isCollapsed class
    // if is rtl
    if (state.rtl && state.appIsCollapsed) return 'is-collapsed-rtl'
    // if is ltr
    else if (!state.rtl && state.appIsCollapsed) return 'is-collapsed'
    // default class
    else return ''
  }
}

export default state



