const mutations = {
  rtlSet: (state, payload) => {
    state.rtl = payload.payload
  },
  appIsCollapsedSet: (state, payload) => {
    state.appIsCollapsed = payload.payload
  },
  appIsCollapsedToggle: (state, payload) => {
    state.appIsCollapsed = !state.appIsCollapsed
  }
}

export default mutations



