<template>
  <div class="layer w-100 mB-20" :class="{'rtl': rtl}">
    <div class="peer d-n@md+">
      <a href="" title="" id='chat-sidebar-toggle' class="td-n c-grey-900 cH-blue-500 mR-30">
        <i class="ti-menu"></i>
      </a>
    </div>
    <h6 class="lh-1" :class="{'ta-r': rtl}">{{t('OVERVIEW')}} {{t('ABOUT')}} {{iname}}:</h6>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
import * as $ from 'jquery'
export default {
  name: 'GymTitle',
  computed: {
    // mix the getters into computed with object spread operator
    ...mapGetters({
      'rtl': 'getRtl'
    })
  },
  data () {
    return {
    }
  },
  props: {
    iname: {
      type: String,
      required: true
    }
  },
  components: {
  },
  mounted () {
    this.$nextTick(function () {
      $('#chat-sidebar-toggle').on('click', e => {
        $('#chat-sidebar').toggleClass('open')
        e.preventDefault()
      })
    })
  }
}
</script>
<style>

</style>



