const actions = {
  sidebarManage: (context, payload) => {
    switch (payload.type) {
      case 'toggle':
        context.commit('appIsCollapsedToggle')
        break
      case 'set':
        context.commit('appIsCollapsedSet', {payload: payload.payload})
        break
    }
  }
}

export default actions



