<template>
  <div id="app" class="app" :class="getAppIsCollapsedClass">
    <!-- <img src="./assets/logo.png"> -->
    <router-view/>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'App',
  computed: {
    ...mapGetters([
      'getAppIsCollapsedClass'
    ])
  },
  mounted () {
    this.$translate.setLang('farsi')
  }
}
</script>

<style lang="scss">

</style>



